@charset "UTF-8";
html {
  scroll-behavior: smooth; }

body {
  font-family: "PT Sans", sans-serif;
  color: #737373;
  overflow-x: hidden; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Cormorant Infant", serif;
  color: #262626; }

main {
  background: #f4f4f4; }

.section-wrapper {
  margin: 0 auto;
  max-width: 1136px; }
  @media (max-width: 1200px) {
    .section-wrapper {
      padding: 0 20px; } }

* {
  box-sizing: border-box;
  margin: 0; }

body {
  padding: 0; }

a {
  text-decoration: none;
  color: inherit;
  outline: none;
  cursor: pointer; }

ul {
  list-style-type: none;
  padding: 0; }

button {
  background-color: #fff;
  cursor: pointer;
  font-family: inherit;
  border: none;
  outline: none; }

@keyframes fadeIn-translate-top {
  from {
    opacity: 0;
    transform: translateY(-20px); }
  to {
    opacity: 0.95;
    transform: translateY(0); } }

@keyframes fadeIn-translate-left {
  from {
    opacity: 0;
    transform: translateX(-40px); }
  to {
    opacity: 1;
    transform: translateY(0); } }

@keyframes fadeIn-translate-right {
  from {
    opacity: 0;
    transform: translateX(40px); }
  to {
    opacity: 1;
    transform: translateY(0); } }

@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

