@media (max-width: 680px) {
  .menu-toggle {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
    height: 20px;
    width: 25px;
    cursor: pointer;
    z-index: 2; }
    .menu-toggle__part {
      width: 23px;
      height: 4px;
      background-color: #000;
      border-radius: 4px;
      transform-origin: left center;
      transition: transform 0.3s ease-in; }
  .menu-toggle--active .menu-toggle__part:nth-child(2) {
    opacity: 0; }
  .menu-toggle--active .menu-toggle__part:nth-child(1) {
    transform: rotate(45deg); }
  .menu-toggle--active .menu-toggle__part:nth-child(3) {
    transform: rotate(-45deg); }
  .navigation__list--active {
    position: absolute;
    top: 5px;
    left: -20px;
    display: flex;
    padding: 150px;
    flex-direction: column;
    height: calc(100vh - 150px);
    width: calc(100vw - 17px);
    z-index: 1;
    background-color: #f4f4f4; }
    .navigation__list--active .navigation__item {
      margin: 0 auto; }
    .navigation__list--active .navigation__link {
      transition: color 0.3s;
      font-size: 16px;
      font-weight: 600; }
      .navigation__list--active .navigation__link:hover {
        box-shadow: none;
        color: #48974c; } }

@media (max-width: 510px) {
  .navigation__list--active {
    padding: 80px 30px;
    display: block; } }

